import {
  IntrinsicNodeProps,
  View,
  Text,
  ElementNode,
  activeElement,
  hexColor,
} from "@lightningjs/solid";
import styles from "./ListSlider.style";
import { For, createEffect, createSelector, createSignal, on } from "solid-js";
import { assertTruthy } from "@lightningjs/renderer/utils";
import { FocusRing, TileRow } from "..";
import theme from "theme";
import { Column, Row } from "@lightningjs/solid-primitives";
import { navigate } from "@lightningjs/sdk/src/Router";
import Thumbnail from "../Thumnail";
export default function ListSlider(props: IntrinsicNodeProps) {
  console.log(props.type);
  let focusRingRef;
  const isFirst = createSelector(() => {
    return 0;
  });

  const [rowX, setRowX] = createSignal(props.x);

  function moveRow(row) {
    console.log("row", row);
    if(row.selected === 0){
      setRowX(90)
    }else {
      setRowX(row.selected * -row.children[0]._width + 10);
    }
    
  }
  function onEnter(this: ElementNode) {
    let entity = this.children.selected;
    assertTruthy(entity && entity.id);
    navigate("/" + entity.id);
  }

  // console.log('props',props)
  return (
    <View clipping style={styles.itemsContainer}>
      <TileRow
        animate
        gap={10}
        x={rowX()}
        y={props.y}
        width={4400}
        style={styles.Row}
        justifyContent={"flexStart"}
        autofocus={isFirst(0)}
        onSelectedChanged={moveRow}
        items={props.data}
        onFocus={props.onFocus}
        onEnter={props.onEnter}
      />
    </View>
  );
}
